.btn {
  transition: background 250ms;
  font-weight: bold;
  -webkit-box-shadow: 0px 3px 3px 0 rgba(0, 0, 0, 0.15);
  box-shadow: 0px 3px 3px 0 rgba(0, 0, 0, 0.15);
  border-radius: 6px;
  font-weight: 600;
  letter-spacing: .9px;
  padding: 10px 18px;
}
.btn.btn-blank {
  background: transparent;
  color: #2ea049;
  -webkit-box-shadow: none;
  box-shadow: none;
}
.btn.btn-blank:hover {
  text-decoration: underline;
}
.btn.btn-blank:hover .text-normal {
  text-decoration: none;
  text-decoration-line: none;
}
.btn.btn-blank .text-normal {
  color: #353937;
  font-weight: normal;
  letter-spacing: normal;
  font-size: 14px;
  text-decoration: none;
}
.btn.btn-muted {
  color: #5a5e5a;
}
.btn.btn-muted:hover,
.btn.btn-muted:focus {
  text-decoration: none;
  color: #000;
}
.form-control {
  -webkit-box-shadow: none;
  box-shadow: none;
  border-width: 2px;
  border-top: none;
  border-right: none;
  font-weight: 600;
  padding-left: 8px;
}
.form-control:focus {
  -webkit-box-shadow: none;
  box-shadow: none;
}
.form-control-wrap {
  position: relative;
  display: block;
}
.form-control-wrap:before {
  content: "";
  display: block;
  position: absolute;
  left: 0px;
  top: 0px;
  height: calc(100% - 8px);
  width: 3px;
  background: #fff;
  z-index: 4;
}
.form-control-wrap .gutter-icon {
  position: absolute !important;
  top: 9px;
  right: -24px;
}
.control-label {
  color: #353937 !important;
  font-size: 12px;
  font-weight: normal;
}
.control-label .text-muted {
  font-weight: normal;
  color: #71767f;
  margin-left: 8px;
}
.control-label.has-focus {
  color: #000;
}
.form-control-feedback {
  opacity: .5;
  transition: opacity 250ms;
}
.form-control:focus + .form-control-feedback,
.form-control:focus + * .form-control-feedback {
  opacity: 1;
}
a.form-control-feedback {
  pointer-events: auto;
  color: #000;
}
a.form-control-feedback:hover {
  opacity: 1;
}
.dropdown-menu {
  border-radius: 9px;
  -webkit-box-shadow: 0px 5px 10px 0 rgba(0, 0, 0, 0.2);
  box-shadow: 0px 5px 10px 0 rgba(0, 0, 0, 0.2);
}
.navbar-nav > li > .dropdown-menu {
  border-radius: 9px;
}
.open > .dropdown-menu {
  animation-name: slidenavAnimation;
  animation-duration: .2s;
  animation-iteration-count: 1;
  animation-timing-function: ease;
  animation-fill-mode: forwards;
  -webkit-animation-name: slidenavAnimation;
  -webkit-animation-duration: .2s;
  -webkit-animation-iteration-count: 1;
  -webkit-animation-timing-function: ease;
  -webkit-animation-fill-mode: forwards;
  -moz-animation-name: slidenavAnimation;
  -moz-animation-duration: .2s;
  -moz-animation-iteration-count: 1;
  -moz-animation-timing-function: ease;
  -moz-animation-fill-mode: forwards;
}
@keyframes slidenavAnimation {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-webkit-keyframes slidenavAnimation {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
