  transition: background 250ms;
}
.progress-bar-danger {
  background-color: #e60303;
}
.progress-bar-warning {
  background-color: #fd7f23;
}
.progress-bar-ok {
  background-color: #f2c700;
}
.progress-bar-success {
  background-color: #81c241;
}
